import React, { useCallback, useMemo, useState } from 'react';
import {
  Box, Button, FormControl, FormLabel, Input
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import MiniBox from '../../../layout/MiniBox';
import AddressSearch from '../../../components/AddressSearch';
import { updateSelectedContact } from '../../../store/actionCreators/contact';
import { useDrawer } from '../../../hooks/drawer';

const InternationalWirePayment = React.lazy(() => import('./InternationalWirePayment'));

const InternationalWireAddress:React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const { open } = useDrawer();

  const contactDetails = useAppSelector((store) => store.contact.selectedContact, shallowEqual);
  const [localContactDetails, updateContactData] = useState({
    name: contactDetails?.name || '',
    address: contactDetails?.wire?.international?.beneficiaryAddress || {
      line1: '',
      line2: '',
      city: '',
      state: '',
      country: '',
      postalCode: '',
      addressType: 'legal',
    }
  });

  const isValid = useMemo(() => (
    localContactDetails
      && !!localContactDetails.address
      && !!localContactDetails.address.line1
      && !!localContactDetails.address.city
      && !!localContactDetails.address.state
      && !!localContactDetails.address.country
      && !!localContactDetails.address.postalCode
      && localContactDetails.address.line1.length !== 0
      && localContactDetails.address.city.length !== 0
      && localContactDetails.address.state.length !== 0
      && localContactDetails.address.country.length !== 0
      && localContactDetails.address.postalCode.length !== 0
  ), [localContactDetails]);

  const submit = useCallback(() => {
    if (contactDetails?.id) {
      dispatch(updateSelectedContact({
        ...contactDetails,
        wire: {
          ...contactDetails.wire,
          international: {
            ...contactDetails.wire?.international,
            beneficiaryAddress: localContactDetails.address
          }
        }
      }));

      open(InternationalWirePayment);
    }
  }, [localContactDetails, contactDetails]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          heading={t('payments_international_wire_address.title')}
          footer={(
            <Button width="100%" variant="primary" disabled={!isValid} onClick={submit}>
              {t('payments_international_wire_address.ctaText')}
            </Button>
          )}

        >
          <Box width="100%" paddingTop="15px">

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_international_wire_address.name')}
              </FormLabel>
              <Input
                variant="primary"
                value={localContactDetails?.name || ''}
                onChange={(e) => {
                  updateContactData({
                    ...localContactDetails,
                    name: e.target.value
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_international_wire_address.address1')}
              </FormLabel>
              <AddressSearch
                placeholder={t('payments_international_wire_address.address1')}
                googleApiKey={process.env.REACT_APP_GMAP_KEY}
                international
                address={localContactDetails?.address || undefined}
                onChange={(address) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      ...address,
                      addressType: 'legal',
                    }
                  });
                }}
              />
              {/* <Input variant="primary" value="" /> */}
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_international_wire_address.address2')}
              </FormLabel>
              <Input
                variant="primary"
                value={localContactDetails?.address?.line2}
                placeholder={t('payments_international_wire_address.address2')}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      line2: e.target.value,
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_international_wire_address.addressCity')}
              </FormLabel>
              <Input
                variant="primary"
                value={localContactDetails?.address?.city}
                placeholder={t('payments_international_wire_address.addressCity')}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      city: e.target.value,
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_international_wire_address.addressState')}
              </FormLabel>
              <Input
                variant="primary"
                value={localContactDetails?.address?.state}
                placeholder={t('payments_international_wire_address.addressState')}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      state: e.target.value,
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_international_wire_address.addressZipcode')}
              </FormLabel>
              <Input
                variant="primary"
                value={localContactDetails?.address?.postalCode}
                placeholder={t('payments_international_wire_address.addressZipcode')}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      postalCode: e.target.value,
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_international_wire_address.addressCountry')}
              </FormLabel>
              <Input
                variant="primary"
                value={localContactDetails?.address?.country}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      country: e.target.value,
                    }
                  });
                }}
              />
            </FormControl>

          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default InternationalWireAddress;
